export default {
    data () {
        return {
            checkedKind:"重庆时时彩1",
            showKind:true,
            show2:false,
            current:0,
            current1:0,
            current2:0,
            helpMenus:["时间","准确率","点击率"],
            playList:[
               "定位胆","不定位胆","直选","任意单式","定位大小","定位单双"
            ],
            kindList:[
              "重庆时时彩","重庆时时彩1","重庆时时彩2","重庆时时彩3"
            ],
            seriesList:[
               "万位","千位","百位","十位","个位"
            ]
        }
    },
    methods : {
        helpAdd (index) {
            this.current=index;
        },
        checkKind (index) {
            console.log(this.kindList[index])
           this.checkedKind=this.kindList[index];
           this.showKind=false;
        },
        showSeries (index)  {
             this.current1=index;
        },
        seriesActive(index){
            this.current2=index;
        },
        toItem () {
            this.$router.push({path:'/item'}); 
        }
    }
}



